import {createReducer, updateIn} from 'redux-decorated'
import {actions} from '../actions'

export const pages = createReducer({})
  .when(actions.updatePages, (state, pages) =>
    pages.reduce((state, page) => updateIn(page.id, page, state), state))

export const selectablePages = createReducer({_loading: {}})
  .when(actions.loadingSelectabePages, (state, network) =>
    updateIn(['_loading', network], true, state))
  .when(actions.loadedSelectabePages, (state, network) =>
    updateIn(['_loading', network], false, state))
  .when(actions.updateSelectablePages, (state, {network, pages}) => updateIn(network, pages, state))
  .when(actions.setSelectablePageActive, (state, {network, pageId, active}) =>
    updateIn([network, pageId, 'active'], active, state))
